/* eslint angular/timeout-service: 0 */
(function() {
  'use strict';

  angular
    .module('crossoverAssignment', [
      'ngAnimate',
      'ngSanitize',
      'ngMessages',
      'LocalStorageModule',
      'angular-input-stars',
      'ngAria',
      'ui.router',
      'angular-md5',
      'toastr'
    ]);

  /**
   * This timeout delays the bootstrap of the application in 2 seconds in order to show the loading animation,
   * simulating the loading of the application on the internet.
   */
  setTimeout(function() {
    angular.bootstrap(document, ['crossoverAssignment']);
  }, 2000);

})();

(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .factory('VideoService', VideoService);

  /** @ngInject */
  function VideoService($http, SERVER) {
    var factory = {
      getVideos: getVideos,
      getVideo: getVideo,
      rateVideo: rateVideo
    };
    return factory;

    function getVideos(sessionId, skip, limit) {
      return $http({
        method: 'GET',
        url: SERVER.ADDRESS + '/videos',
        headers: {
          'Content-Type': 'application/json'
        },
        params: {
          sessionId: sessionId,
          skip: skip,
          limit: limit
        }
      })
    }

    function getVideo(sessionId, videoId) {
      return $http({
        method: 'GET',
        url: SERVER.ADDRESS + '/video',
        headers: {
          'Content-Type': 'application/json'
        },
        params: {
          sessionId: sessionId,
          videoId: videoId
        }
      })
    }

    function rateVideo(sessionId, videoId, rating) {
      return $http({
        method: 'POST',
        url: SERVER.ADDRESS + '/video/ratings',
        headers: {
          'Content-Type': 'application/json'
        },
        params: {
          sessionId: sessionId
        },
        data: {
          videoId: videoId,
          rating: rating
        }
      })
    }
  }
})();

(function() {
  'use strict';

  /** @ngInject */
  var videoPlayer = {
    bindings: {
      videoId: '@',
      name: '@',
      description: '@',
      url: '<'
    },
    controller: function ($scope, $element, $rootScope, SERVER) {
      var previousRate = null;

      this.$onInit = function() {
        var video = angular.element($element).find('video');

        /**
         * This listener runs when a video starts to play and pauses all others from the page.
         */
        video.on('play', function() {
          angular.forEach(angular.element('video'),
            function(current) {
              if (video.get(0).id != current.id)
                current.pause();
            });
        });
      };

      this.$onChanges = function () {
        /**
         * Concatenation of the the server address with the video path.
         * @type {string}
         */
        this.url = SERVER.ADDRESS.concat('/', this.url);
      };

      /**
       * This method makes sure that "videoRating" parameter will not be 0.
       */
      this.onRatingClick = function() {
        if ($scope.videoRating == 0)
          $scope.videoRating = previousRate;

        previousRate = $scope.videoRating;

        /**
         * This code triggers the event "onRatingClick" passing "videoId" and "rating" as parameters.
         */
        $rootScope.$emit('onRatingClick', {
          videoId: this.videoId,
          rating: $scope.videoRating
        });
      }
    },
    templateUrl: 'app/components/videoPlayer/videoPlayer.html'
  };

  angular
    .module('crossoverAssignment')
    .component('videoPlayer', videoPlayer);

})();

/* eslint no-unused-vars: 0 */
(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .directive('videoBlock', videoBlock);

  /** @ngInject */
  function videoBlock(SERVER) {
    return {
      restrict: 'E',
      bindToController: {
        videoId: '@',
        name: '@',
        description: '@',
        url: '<',
        ratings: '<'
      },
      scope: {},
      controller: function($scope, $rootScope) {
        var $ctrl = this;

        $ctrl.average = average;
        $ctrl.round = round;

        /**
         * This method returns the average of an array of numbers.
         * @param array
         * @returns {number}
         */
        function average(array) {
          var sum = array.reduce(function(total, num) {
            return total + num;
          });

          return sum / array.length;
        }

        /**
         * This method rounds a value based on the "step" parameter.
         * @param value
         * @param step
         * @returns {number}
         */
        function round(value, step) {
          step || (step = 1.0);
          var inv = 1.0 / step;

          return Math.round(value * inv) / inv;
        }

        /**
         * This method assigns the "averageRate" variable the value of the "ratings" variable after it has been
         * processed by the "average" and "round" methods.
         */
        function setVideoRating() {
          $scope.averageRate =
            round(average($ctrl.ratings), 0.5);
        }

        /**
         * This method concatenates the server address with the video path.
         */
        function setVideoUrl() {
          $ctrl.url = SERVER.ADDRESS.concat('/', $ctrl.url);
        }

        /**
         * This code triggers the event "onClickDetailVideo" passing "videoId" as parameter.
         */
        $ctrl.goToVideoDetail = function() {
          $rootScope.$emit('onClickDetailVideo', $ctrl.videoId)
        };

        /**
         * This method runs at template initialization and calls "setVideoRating" and "setVideoUrl" methods.
         */
        $ctrl.onInit = function() {
          setVideoRating();
          setVideoUrl();
        }
      },
      controllerAs: '$ctrl',
      templateUrl: 'app/components/videoBlock/videoBlock.html',
      link: function(scope, el) {
        /**
         * This method register a listener to run when a video starts to play and pauses all others from the page.
         */
        function onPlayVideo() {
          var video = angular.element(el).find('video');

          video.on('play', function() {
            angular.forEach(angular.element('video'),
              function(current) {
                if (video.get(0).id != current.id)
                current.pause();
              });
          });
        }

        (function onInit() {
          onPlayVideo()
        })();
      }
    }
  }
})();

