/* eslint no-unused-vars: 0 */
(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .directive('loadingScreen', loadingScreen);

  /** @ngInject */
  function loadingScreen($animate) {
    var directive = {
      restrict: 'C',
      link: function(scope, element, attributes) {
        $animate.enabled(true);

        /**
         * This listener removes the DOM element and clears its state.
         */
        $animate.leave(element.children().eq(0)).then( function() {
          element.remove();
          scope = element = attributes = null;
          $animate.enabled(false);
        });
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .factory('AuthService', AuthService);

  /** @ngInject */
  function AuthService($http, md5, SERVER, localStorageService) {
    var factory = {
      auth: auth,
      logout: logout,
      getSessionId: getSessionId
    };
    return factory;

    function auth(username, password) {
      return $http({
        method: 'POST',
        url: SERVER.ADDRESS + '/user/auth',
        headers: {
          'Content-Type': 'application/json'
        },
        data: {
          username: username,
          password: md5.createHash(password)
        }
      })
    }

    function logout(sessionId) {
      return $http({
        method: 'GET',
        url: SERVER.ADDRESS + '/user/logout',
        headers: {
          'Content-Type': 'application/json'
        },
        params: { sessionId: sessionId }
      })
    }

    function getSessionId() {
      return localStorageService.get('sessionId');
    }
  }
})();

/* eslint no-undef: 0, angular/document-service: 0 */
(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .controller('VideoListController', VideoListController);

  /** @ngInject */
  function VideoListController($timeout, $state, toastr, AuthService, VideoService) {
    var vm = this;

    var NUMBER_VIDEOS_TO_LOAD = 10;
    var SHOULD_INIT_SCROLLMONITOR = true;

    vm.videos = [];
    vm.showSpinner = false;

    vm.onInit = onInit;
    vm.getVideos = getVideos;

    /**
     * This method runs at template initialization and loads the first videos.
     */
    function onInit() {
      vm.getVideos(AuthService.getSessionId(), vm.videos.length, NUMBER_VIDEOS_TO_LOAD);
    }

    /**
     * This method accesses the server to load a set of videos based on the "skip" and "limit" params.
     * @param sessionId
     * @param skip
     * @param limit
     */
    function getVideos(sessionId, skip, limit) {
      VideoService.getVideos(sessionId, skip, limit)
        .then(function(response) {
          vm.videos = vm.videos.concat(response.data.data);

          vm.showSpinner = false;

          if (SHOULD_INIT_SCROLLMONITOR) {
            $timeout(function() {
              initScrollMonitor();
            });

            SHOULD_INIT_SCROLLMONITOR = false;
          }
        })
        .catch(function() {
          toastr.error(null, 'Could not load videos.');

          $state.go('login')
        })
      ;
    }

    /**
     * This method initializes and configures the "scrollMonitor" module to allow loading videos as the user scroll down
     * the page (lazy loading feature).
     */
    function initScrollMonitor() {
      scrollMonitor
        .create(document.getElementById('video-list-end'))
        .enterViewport(function() {
          if (vm.videos.length < 101) {
            vm.showSpinner = true;
            vm.getVideos(AuthService.getSessionId(), vm.videos.length, NUMBER_VIDEOS_TO_LOAD);
          }
        })
      ;
    }
  }
})();

/* eslint angular/on-watch: 0 */
(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .controller('VideoDetailController', VideoDetailController);

  /** @ngInject */
  function VideoDetailController($stateParams, toastr, VideoService, AuthService) {
    var vm = this;

    var NUMBER_VIDEOS_TO_LOAD = 5;

    vm.video = null;
    vm.videos = [];
    vm.results = [];

    vm.onInit = onInit;
    vm.getVideo = getVideo;
    vm.getVideos = getVideos;
    vm.showMoreSideVideos = showMoreSideVideos;

    /**
     * This method runs at template initialization and loads the current video based on the "id" url param.
     */
    function onInit() {
      vm.getVideo(AuthService.getSessionId(), $stateParams.id);
    }

    /**
     * This method accesses the server to load one video based on the videoId param.
     * @param sessionId
     * @param videoId
     */
    function getVideo(sessionId, videoId) {
      VideoService.getVideo(sessionId, videoId)
        .then(function(response) {
          vm.video = response.data.data;

          vm.getVideos(AuthService.getSessionId(), vm.videos.length, NUMBER_VIDEOS_TO_LOAD);
        })
        .catch(function() {
          toastr.error(null, 'Could not load video '.concat(videoId, '.'));
        })
      ;
    }

    /**
     * This method accesses the server to load a set of videos based on the "skip" and "limit" params.
     * @param sessionId
     * @param skip
     * @param limit
     */
    function getVideos(sessionId, skip, limit) {
      VideoService.getVideos(sessionId, skip, limit)
        .then(function(response) {
          var data = response.data.data;

          vm.videos = vm.videos.concat(data);

          data = data.filter(function(element) {
            return element._id != vm.video._id;
          });

          vm.results = vm.results.concat(data);
        })
        .catch(function() {
          toastr.error(null, 'Could not load videos.');
        })
      ;
    }

    /**
     * This method triggers when the user clicks the "Show More" button and accesses the "getVideos" method to load
     * more videos from the server.
     */
    function showMoreSideVideos() {
      vm.getVideos(AuthService.getSessionId(), vm.videos.length, NUMBER_VIDEOS_TO_LOAD);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($state, AuthService, toastr, localStorageService) {
    var vm = this;

    vm.auth = auth;

    /**
     * This method requests the server to authenticate a user based on the "username" and "password" params.
     * @param username
     * @param password
     */
    function auth(username, password) {
      AuthService.auth(username, password)
        .then(function(response) {
          if (response.data.status == 'error') {
            toastr.error(null, response.data.error);
          } else {
            localStorageService.set('sessionId', response.data.sessionId);

            $state.go('videoList');
          }
        })
        .catch(function() {
          toastr.error(null, 'Could not connect to server. Try again later.');
        })
      ;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .filter('truncateDescription', truncateDescription);

  /**
   * This filter crops the string parameter to 85 characters and adds the string "..." to the end.
   * @returns {Function}
   */
  function truncateDescription() {
    return function(input) {
      return input.substring(0, 85).concat('...');
    };
  }

})();

(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .run(runBlock);

  /** @ngInject */
  function runBlock($state, $rootScope, toastr, localStorageService, AuthService, VideoService) {
    /**
     * All the "$on" calls here is assigned to a variable, in order to be destroyed during the $destroy event.
     */

    /**
     * This listener is triggered when a state changes and checks for routes that are private. If there is no sessionId
     * stored in the local storage the user is redirected to the Log In page.
     */
    var onStateChangeSuccess = $rootScope.$on('$stateChangeSuccess',
      function(event, toState) {
        if ((toState.permission === 'private') && (!localStorageService.get('sessionId'))) {
          $state.go('login');

          toastr.error(null, 'Not authorized. Try to sign in again.');
        }
      })
    ;
    $rootScope.$on('$destroy', onStateChangeSuccess);

    /**
     * This method returns whether there is a sessionId in local storage.
     * @returns {boolean}
     */
    $rootScope.isLogged = function() {
      return !!localStorageService.get('sessionId');
    };

    /**
     * This listener requests the log out to the server. Removing the sessionId from local storage and redirecting to the
     * login page.
     */
    var onLogout = $rootScope.$on('logout', function() {
      AuthService.logout(AuthService.getSessionId())
        .then(function() {
          localStorageService.remove('sessionId');

          $state.go('login');
        })
      ;
    });
    $rootScope.$on('$destroy', onLogout);

    /**
     * This listener executes when the "onClickDetailVideo" event is fired and redirects to the video detail page by
     * passing the video id as parameter.
     */
    var onClickDetailVideo = $rootScope.$on('onClickDetailVideo', function(event, args) {
      $state.go('videoDetail', { 'id': args });
    });
    $rootScope.$on('$destroy', onClickDetailVideo);

    /**
     * This listener executes when the "onRatingClick" event is triggered and requests the server to evaluate the video
     * passed as parameter.
     */
    var onRatingClick = $rootScope.$on('onRatingClick', function(event, args) {
      VideoService.rateVideo(AuthService.getSessionId(), args.videoId, args.rating)
        .then(function() {
          toastr.success(null, 'VÃ­deo rated.');
        })
        .catch(function() {
          toastr.error(null, 'Could not rate this video.');
        })
      ;
    });
    $rootScope.$on('$destroy', onRatingClick);
  }

})();

(function() {
  'use strict';

  angular
    .module('crossoverAssignment')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('login', {
        url: '/login',
        templateUrl: 'app/login/login.html',
        controller: 'LoginController',
        controllerAs: 'vm',
        permission: 'public'
      })
      .state('videoList', {
        url: '/videoList',
        templateUrl: 'app/videoList/videoList.html',
        controller: 'VideoListController',
        controllerAs: 'vm',
        permission: 'private'
      })
      .state('videoDetail', {
        url: '/videoDetail/:id',
        templateUrl: 'app/videoDetail/videoDetail.html',
        controller: 'VideoDetailController',
        controllerAs: 'vm',
        permission: 'private'
      });

    $urlRouterProvider.otherwise('/login');
  }

})();

